                     :search-input.sync=\"oficinas_search\"\r\n                            required\r\n                        >\r\n                            <template v-slot:no-data>\r\n                                <v-list-item>\r\n                                    <v-list-item-title>\r\n                                        <template\r\n                                            v-if=\"oficinas_search?.length > 2\"\r\n                                        >\r\n                                            Datos no encontrados para\r\n                                            <strong>\r\n                                                {{ oficinas_search }}\r\n                                            </strong>\r\n                                        </template>\r\n                                        <template v-else>\r\n                                            Digite más de\r\n                                            <strong> 2</strong> caracteres.\r\n                                        </template>\r\n                                    </v-list-item-title>\r\n                                </v-list-item>\r\n                            </template>\r\n                            <template v-slot:selection=\"data\">\r\n                                <small>\r\n                                    <strong>{{ data.item.codigo }}</strong>\r\n                                    {{ data.item.nombre }}</small\r\n                                >\r\n                            </template>\r\n                            <template v-slot:item=\"data\">\r\n                                <v-list-item-content>\r\n                                    <v-list-item-title\r\n                                        v-html=\"data.item.codigo\"\r\n                                    >\r\n                                    </v-list-item-title>\r\n                                    <v-list-item-subtitle>\r\n                                        {{ data.item.nombre }}\r\n                                    </v-list-item-subtitle>\r\n                                </v-list-item-content>\r\n                            </template>\r\n                        </v-autocomplete>\r\n                    </v-col>\r\n                    <v-col cols=\"12\" class=\"pb-1 pt-0\">\r\n                        <v-autocomplete\r\n                            clearable\r\n                            class=\"mt-0 pt-0\"\r\n                            dense\r\n                            label=\"Area\"\r\n                            outlined\r\n                            :items=\"areas_by_oficna\"\r\n                            item-text=\"nombre\"\r\n                            item-value=\"id\"\r\n                            v-model=\"area_selected\"\r\n                            required\r\n                        ></v-autocomplete>\r\n                    </v-col>\r\n                </v-row>\r\n\r\n                <v-card :loading=\"loading_table\">\r\n                    <v-overlay absolute :value=\"loading_table\">\r\n                        <v-progress-circular\r\n                            indeterminate\r\n                            size=\"64\"\r\n                        ></v-progress-circular>\r\n                    </v-overlay>\r\n                    <v-card-title>\r\n                        <v-row>\r\n                            <v-col cols=\"12\" sm=\"5\" class=\"pb-1\">\r\n                                <v-combobox\r\n                                    v-model=\"mostrar_selected\"\r\n                                    :items=\"items_combobox\"\r\n                                    label=\"Mostrar\"\r\n                                    outlined\r\n                                    hide-details\r\n                                    dense\r\n                                ></v-combobox>\r\n                            </v-col>\r\n                            <v-col cols=\"12\" sm=\"7\" class=\"pb-1\">\r\n                                <v-text-field\r\n                                    v-model=\"area_search\"\r\n                                    append-icon=\"mdi-magnify\"\r\n                                    label=\"Buscar\"\r\n                                    hide-details\r\n                                    outlined\r\n                                    dense\r\n                                ></v-text-field>\r\n                            </v-col>\r\n                        </v-row>\r\n                    </v-card-title>\r\n                    <v-simple-table>\r\n                        <template v-slot:default>\r\n                            <thead class=\"grey lighten-3\">\r\n                                <tr>\r\n                                    <th class=\"text-left\">Estado</th>\r\n                                    <th class=\"text-left\">Nombre</th>\r\n                                    <th class=\"text-left\">Codigo</th>\r\n                                    <th class=\"text-left\">Cod. Ant.</th>\r\n                                </tr>\r\n                            </thead>\r\n\r\n                            <tbody>\r\n                                <tr\r\n                                    v-for=\"(item, index) in bienes_result\"\r\n                                    :key=\"index\"\r\n                                    :class=\"[\r\n                                        index == tr_index ? 'tr-selected' : '',\r\n                                    ]\"\r\n                                    @click=\"onSelectColum(item, index)\"\r\n                                    @dblclick=\"onSelectColumDobleClik(item)\"\r\n                                    disabled\r\n                                >\r\n                                    <td>\r\n                                        <template v-if=\"item.id_inventario\">\r\n                                            <v-chip\r\n                                                small\r\n                                                color=\"green\"\r\n                                                outlined\r\n                                                class=\"ma-2\"\r\n                                            >\r\n                                                <small>REGISTRADO</small>\r\n                                            </v-chip>\r\n                                        </template>\r\n                                        <template\r\n                                            v-else-if=\"\r\n                                                (item.id_inventario &&\r\n                                                item.bloqueado)\r\n                                            \"\r\n                                        >\r\n                                            <v-chip\r\n                                                small\r\n                                                color=\"grey\"\r\n                                                outlined\r\n                                                class=\"ma-2\"\r\n                                            >\r\n                                                <small>NO REGISTRADO</small>\r\n                                            </v-chip>\r\n                                        </template>\r\n                                        <template v-else>\r\n                                            <v-chip\r\n                                                small\r\n                                                color=\"orange\"\r\n                                                outlined\r\n                                                class=\"ma-2\"\r\n                                            >\r\n                                                <small>NO REGISTRADO</small>\r\n                                            </v-chip>\r\n                                        </template>\r\n                                    </td>\r\n                                    <td>{{ item.nombre }}</td>\r\n                                    <td>{{ item.codigo }}</td>\r\n                                    <td>\r\n                                        {{ item.codigo_anterior }}\r\n                                    </td>\r\n                                </tr>\r\n                            </tbody>\r\n                        </template>\r\n                    </v-simple-table>\r\n\r\n                    <v-card-actions>\r\n                        <v-spacer></v-spacer>\r\n                        <v-pagination\r\n                            v-model=\"page\"\r\n                            class=\"\"\r\n                            :length=\"pages\"\r\n                        ></v-pagination>\r\n                        <v-spacer></v-spacer>\r\n                    </v-card-actions>\r\n                </v-card>\r\n            </v-card-text>\r\n            <v-divider></v-divider>\r\n\r\n            <v-card-actions>\r\n                <v-spacer></v-spacer>\r\n                <v-btn small color=\"primary\" @click=\"SeleccionarBien\">\r\n                    <v-icon left>mdi-check</v-icon> Seleccionar\r\n                </v-btn>\r\n            </v-card-actions>\r\n        </v-card>\r\n    </v-dialog>\r\n</template>\r\n<script>\r\nimport axios from \"axios\";\r\n\r\nexport default {\r\n    data: () => ({\r\n        loading_table: false,\r\n\r\n        tr_index: null,\r\n        tr_item: {},\r\n\r\n        items_combobox: [\"Todos\", \"Registrados\", \"Sin registrar\"],\r\n\r\n        dialog: false,\r\n        area_selected: null,\r\n        search: \"\",\r\n\r\n        bienes_result: [],\r\n        mostrar_selected: \"Todos\",\r\n        area_search: \"\",\r\n\r\n        page: 1,\r\n        total_result: 0,\r\n        pages: 1,\r\n\r\n        oficina_selected: null,\r\n        oficinas_res: [],\r\n        oficinas_search: \"\",\r\n\r\n        area_selected: null,\r\n        areas_by_oficna: [],\r\n    }),\r\n\r\n    methods: {\r\n        async Editar(id) {\r\n            let res = await axios.get(\"/inventario/get-inventario/\" + id);\r\n\r\n            console.log(res.data);\r\n        },\r\n\r\n        onSelectColum(item, index) {\r\n            this.tr_index = index;\r\n            this.tr_item = item;\r\n        },\r\n        onSelectColumDobleClik(item) {\r\n            console.log(\"Doble clic\");\r\n            this.$emit(\"setData\", item);\r\n            this.dialog = false;\r\n        },\r\n        SeleccionarBien() {\r\n            this.$emit(\"setData\", this.tr_item);\r\n            this.dialog = false;\r\n        },\r\n\r\n        async getBienes(area, term = \"\", mostrar = \"Todos\", page = 1) {\r\n            let res = await axios.post(\"/inventario/get-bienes?page=\" + page, {\r\n                area: area,\r\n                mostrar: mostrar,\r\n                term: term,\r\n            });\r\n\r\n            this.page = res.data.datos.current_page;\r\n            this.total_result = res.data.datos.total;\r\n            this.pages = res.data.datos.last_page;\r\n\r\n            return res.data.datos.data;\r\n        },\r\n\r\n        customFilterOficina(item, queryText, itemText) {\r\n            const nombre = item.nombre.toLowerCase();\r\n            const codigo = item.codigo.toLowerCase();\r\n            const searchText = queryText.toLowerCase();\r\n            return (\r\n                nombre.indexOf(searchText) > -1 ||\r\n                codigo.indexOf(searchText) > -1\r\n            );\r\n        },\r\n\r\n        async BuscarOficinas(term) {\r\n            let res = await axios.get(\"/inventario/search-oficinas/\" + term);\r\n            return res.data.datos;\r\n        },\r\n    },\r\n\r\n    watch: {\r\n        async area_selected(val) {\r\n            if (!val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(val);\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async oficinas_search(val) {\r\n            if (!val) return;\r\n            if (val.length < 2) return;\r\n            let res = await this.BuscarOficinas(val);\r\n            this.oficinas_res = res;\r\n        },\r\n\r\n        async oficina_selected(val) {\r\n            console.log(val);\r\n            let res = await axios.get(\"/inventario/search-areas/\" + val);\r\n            this.areas_by_oficna = res.data.datos;\r\n        },\r\n\r\n        async area_search(val) {\r\n            //if (!val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                val,\r\n                this.mostrar_selected\r\n            );\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async page(val, old_val) {\r\n            if (val == old_val) return;\r\n\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                this.area_search,\r\n                this.mostrar_selected,\r\n                val\r\n            );\r\n            this.bienes_result = res;\r\n\r\n            this.loading_table = false;\r\n        },\r\n\r\n        async mostrar_selected(val) {\r\n            this.tr_index = null;\r\n            this.loading_table = true;\r\n            let res = await this.getBienes(\r\n                this.area_selected,\r\n                this.area_search,\r\n                val\r\n            );\r\n            this.bienes_result = res;\r\n            this.loading_table = false;\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n.tr-selected {\r\n    background-color: rgba(0, 0, 255, 0.2);\r\n    border: 1px dashed #444;\r\n    color: blue;\r\n    font-weight: 600;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
